"use client";
import useGlobalStore from "@/store/my-store";
import { Button, Table } from "antd";
import { useState, useMemo } from "react";
import AddOrders from "./edits/AddOrders";
import DeleteOrders from "./edits/DeleteOrders";
import EditOrders from "./edits/EditOrders";

function OrdersPage() {
  const state = useGlobalStore();
  const { orders, products, students, categories } = state;

  const [addOpen, setAddOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [editOrder, setEditOrder] = useState({});
  const [deleteOrder, setDeleteOrder] = useState({});

  const toggleAddDrawer = () => setAddOpen(!addOpen);
  const toggleEditDrawer = () => setEditOpen(!editOpen);

  const handleDeleteAll = () => {
    if (window.confirm("Hammasini o'chirishni tasdiqlaysizmi?")) {
      localStorage.removeItem("orders");
      alert("Hammasi o'chirildi!");
    }
  };

  const columns = useMemo(
    () => [
      {
        title: "ID",
        dataIndex: "id",
        render: (id, order) => (
          <span
            className="cursor-pointer"
            onClick={() => {
              setEditOrder(order);
              toggleEditDrawer();
            }}
          >
            {id}
          </span>
        ),
      },
      {
        title: "Nomi",
        dataIndex: "name",
        render: (name, order) => {
          const product = products.find((i) => i.id === order.productID);
          return product?.name || "Noma'lum";
        },
      },
      {
        title: "Narx",
        render: (order) => {
          const product = products.find((s) => s.id === order.productID);
          return product ? product.price * order.count : 0;
        },
      },
      {
        title: "Mahsulot Soni",
        dataIndex: "count",
      },
      {
        title: "Manzil",
        dataIndex: "address",
      },
      {
        title: "Mijoz",
        dataIndex: "studentID",
        render: (studentID) => {
          const student = students.find((s) => s.id === studentID);
          return student ? student.firstName : "Noma'lum";
        },
      },
      {
        title: "Kategoriyasi",
        dataIndex: "productID",
        render: (productID) => {
          const product = categories.find((p) => p.id === productID);
          return product ? product.name : "Noma'lum";
        },
      },
      {
        title: "Delete",
        render: (text, order) => (
          <DeleteOrders
            studentt={order}
            setDeleteStudent={setDeleteOrder}
            deleteStudent={deleteOrder}
          />
        ),
      },
    ],
    [products, students, categories]
  );

  return (
    <div className="flex flex-col items-center">
      <div className="flex gap-2 items-center justify-center">
        <Button>Umumiy son: {orders.length}</Button>
        <Button>Faollar: {orders.filter((item) => item.active).length}</Button>
        <Button>
          Nofaollar: {orders.filter((item) => !item.active).length}
        </Button>
      </div>
      <div className="flex flex-col my-5">
        <AddOrders onClose={() => setAddOpen(false)} open={addOpen} />
        <EditOrders
          editOpen={editOpen}
          onClose={() => setEditOpen(false)}
          EditStudent={editOrder}
          setEditStudent={setEditOrder}
        />
        <Table
          columns={columns}
          dataSource={orders.map((order) => ({ ...order, key: order.id }))}
        />
        <Button color="danger" type="text" onClick={handleDeleteAll}>
          Hammasini ochirish
        </Button>
      </div>
    </div>
  );
}

export default OrdersPage;
